.scroller {
  height: 100vh;
  overflow-y: scroll;
  scroll-snap-type: y mandatory;
  scroll-behavior: smooth;
  scroll-snap-stop: always;
}

/* .scroller section {
  scroll-snap-align: start;
} */

.section {
  scroll-snap-align: start;
}

.circle {
  display: flex;
  width: 20px;
  height: 20px;
  /* background-color: #86cadd; */
  /* box-shadow: 2px 1px 1px 0 rgb(71, 69, 69), 0 6px 20px 0 rgba(0, 0, 0, 0.19); */
  border-radius: 50%;
  /* margin-top:20vw; */
  /* margin-left: 95vw; */
  background-color: gold;
  box-shadow: 2px 1px 1px 0 rgb(71, 69, 69), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  cursor: pointer;
  position: fixed;
  z-index: 1;
  right: 30px;
}

.dot1 {
  /* margin-top:40vh; */
  top: 40%;
}
.dot2 {
  /* margin-top:45vh; */
  top: 45%;
}
.dot3 {
  /* margin-top:50vh; */
  top: 50%;
}
.dot4 {
  /* margin-top:55vh; */
  top: 55%;
}
.dot5 {
  /* margin-top:60vh; */
  top: 60%;
}
.dot6 {
  /* margin-top:65vh; */
  top: 65%;
}


.circle:hover:after {
  /* content: "صفحه اصلی"; */
  display: inline-block;
  /* z-index: 1; */
  /* background-color: rgba(255,215,0,0.5); */
  background-color: #cccccc;
  width: 8vw;
  height: 2.2vh;
  margin-left: -8vw;
  border-radius: 5px;
  text-align: center;
  font: 0.8em sans-serif;
}

.dot1:hover:after {content: "صفحه اصلی";}
.dot2:hover:after {content: 'سوابق تحصیلی';}
.dot3:hover:after {content: 'مقالات';}
.dot4:hover:after {content: 'مهارت ها';}
.dot5:hover:after {content: 'تجارب';}
.dot6:hover:after {content: 'تماس با ما';}

/* .dot1:active {background-color: aqua;} */
/* .dot2:active {background-color: aqua;} */


/* For mobile: */
@media screen and (max-width: 600px){
  .circle:hover:after {
    width: 15vw;
    margin-left: -15vw;
    transition: all 4s;
  }
}
@media screen and (min-width: 600px){
  .circle:hover:after{transition: all 4s;}
}
