.one{
    margin: 0;
    width: 100vw;
    height: 100vh;
    background-image:url('../../Pic/darkBlue1.jpg');
    position: relative;
    /* position: static; */

    background-color: #cccccc; 
    background-position: center;
    background-repeat: no-repeat; 
    background-size: cover; 
    z-index: 2;
}

.boxAll{
  position: absolute;
  display: grid;
  align-content: center;
  justify-items: center;
  top:50%;
  left: 50%;
  transform: translate3d(-50%,-50%, 0);
  /* background-color: cadetblue; */
}

.boxPicText {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  direction: rtl;

  /* background-color: rgb(0, 47, 255); */
}

.picSara {
  position: relative;
  width: 160px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  border-radius: 8px;
}

.textPageOne{
    display: grid;
    align-content: center;
    justify-items: center;
    background-color: rgba(220, 189, 90, 0.85);
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border-radius: 8px;
    font-weight: 600;
    font-size: medium;
    direction: rtl;
    text-align: center;
    margin-top: auto;
    margin-right: 3px;
    /* padding: 10px 20px 10px 20px; */
    padding: 10px 60px;
    /* color: aliceblue; */
 }

/* Circles: */

.boxCircles {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 90px;
  /* background-color: blueviolet; */
}

.circlePageOne {
  position: relative;
  display: grid;
  align-content: center;
  justify-items: center;
  border-radius: 50%;
  color: aliceblue;
  background-color: rgba(8,46,108,0.85);
  box-shadow: 2px 1px 1px 0 rgb(71, 69, 69), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  width: 140px;
  height: 140px;
  cursor: pointer; 
}

.circlePageOne:hover{ background-color: rgba(220, 189, 90, 0.85); color: black;}
