@media screen and (max-width: 720px){
  .boxAll{
    width: 70vw;
    height: 70vh;
    transition: all 4s;
    /* background-color: #cccccc; */
  }
  .boxPicText{
    /* background-color: crimson; */
    top: -140px;
    transition: all 4s;
  }
  .picSara {
    /* width: 160px; */
    width: 130px;
    transition: all 4s;
  }
  .textPageOne{
    /* padding: 10px 60px; */
    padding: 10px 50px;
    transition: all 4s;
 }
  .boxCircles{
    transform:  translateX(0%) translateY(0%) rotate(-90deg);
    /* margin: 150px 123px; */
    transition: all 4s;

    /* background-color: chartreuse; */
    width: 100%;
    height: 100%;
    top: -90px;
  }
  .circlePageOne {
    border-radius: 10px;
    transform:  translateX(0%) translateY(0%) rotate(90deg);
    /* width: 300px; */
    /* height: 50px; */
    /* margin: 0 -123px; */
    transition: all 4s;

    /* background-color: darkorange; */
    width: 400px;
    height: 50px;
    margin: 0 -171px;
  }
}
@media screen and (min-width: 720px){
  .boxAll{transition: all 4s;}
  .boxPicText{transition: all 4s;}
  .picSara {transition: all 4s;}
  .textPageOne{transition: all 4s;}
  .boxCircles{transition: all 4s;}
  .circlePageOne{transition: all 4s;}
}


